import Vue from 'vue'
import Router from 'vue-router'
import EmployeeTypeRegister from '../EmployeeTypeRegister'
import WorkTimeTypeRegister from '../WorkTimeTypeRegister'
import ApplyConfirm from '../ApplyConfirm'
import EnvironmentRegister from '../EnvironmentRegister'
import login from '../login'
import DayWorkState from '../DayWorkState'
import ApplyStateCheck from '../ApplyStateCheck'
import MonthWorkState from '../MonthWorkState'
import StableAdd from '../StableAdd'
import Home from '../Home'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'WorkTimeTypeRegister',
      component: WorkTimeTypeRegister
    },
    {
      path: '/EmployeeTypeRegister',
      name: 'EmployeeTypeRegister',
      component: EmployeeTypeRegister
    },
    {
      path: '/WorkTimeTypeRegister',
      name: 'WorkTimeTypeRegister',
      component: WorkTimeTypeRegister
    },
    {
      path: '/ApplyConfirm',
      name: 'ApplyConfirm',
      component: ApplyConfirm
    },
    {
      path: '/EnvironmentRegister',
      name: 'EnvironmentRegister',
      component: EnvironmentRegister
    },
    {
      path: '/Home',
      name: 'Home',
      component: Home
    },
    {
      path: '/DayWorkState',
      name: 'DayWorkState',
      component: DayWorkState
    },
    {
      path: '/ApplyStateCheck',
      name: 'ApplyStateCheck',
      component: ApplyStateCheck
    },
    {
      path: '/MonthWorkState',
      name: 'MonthWorkState',
      component: MonthWorkState
    },
    {
      path: '/StableAdd',
      name: 'StableAdd',
      component: StableAdd
    },
    {
      path: '/login',
      name: 'login',
      component: login
    }
  ]
})



