          action: ""
        },{
          code: "開発本部",
          qwtx: ["朝9時固( 9:00～18:00)","朝8時固( 8:00～17:00)","8時間制( 8:00)","フリー(10:00)"],
          qwqf: ["平日", "所休", "法休"],
          xjqf: ["年休", "午前休","午後休","代休","特休"],
          cq: "",
          wc: "",
          fh: "",
          tq: "",
          cqsk: "",
          wcsk: "",
          fhsk: "",
          tqsk: "",
          qw: "",
          cy: "",
          sy: "",
          bk: "",
          sq: "",
          action: ""
        },
        {
          code: "開発本部",
          qwtx: ["朝9時固( 9:00～18:00)","朝8時固( 8:00～17:00)","8時間制( 8:00)","フリー(10:00)"],
          qwqf: ["平日", "所休", "法休"],
          xjqf: ["年休", "午前休","午後休","代休","特休"],
          cq: "",
          wc: "",
          fh: "",
          tq: "",
          cqsk: "",
          wcsk: "",
          fhsk: "",
          tqsk: "",
          qw: "",
          cy: "",
          sy: "",
          bk: "",
          sq: "",
          action: ""
        }
      ],
      editIndex:-1,
      num:'',
    };
  },
  computed: {
    rotateIcon() {
      return ["menu-icon", this.isCollapsed ? "rotate-icon" : ""];
    },
    menuitemClasses() {
      return ["menu-item", this.isCollapsed ? "collapsed-menu" : ""];
    }
  },
  methods: {
    handleSubmit (name) {
        this.$refs[name].validate((valid) => {
            if (valid) {
                this.$Message.success('Success!');
            } else {
                this.$Message.error('Fail!');
            }
        })
    },
    handleReset (name) {
        this.$refs[name].resetFields();
    },
    collapsedSider() {
      this.$refs.side1.toggleCollapse();
    },
    show(index) {
      this.$Modal.info({
        title: "下記の勤務体系を廃止してもよらしいですか？",
        content: 
        `勤務体系コー：${this.data6[index].Num}
        <br>
        勤務体系名：${this.data6[index].Name}
        <br>
        勤務形態：${this.data6[index].Form}`
      });
    },
    remove(index) {
      this.data6.splice(index, 1);
    },
    edit(index){
      this.editIndex = index
    },
    next() {
      if (this.currentStep === 3) {
        return
      } else {
        this.currentStep += 1
      }
    },

    back() {
      if (this.currentStep === 0) {
        return
      } else {
        return (this.currentStep -= 1)
      }
    }
  }
};
</script>




